margin: 0;
padding: 0;
}

button {
  background-color: cornflowerblue;
  color: white;
  font-weight: bold;
  border-style: hidden;
}

nav {
  display: flex;
  flex-direction: column;
  background-color: red;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  padding: 0.5vw 1vw;
  box-sizing: border-box;
  width: 10vw;
  gap: 3vh;
}

main {
  position: relative;
  left: 10vw;
  width: 90vw;
}

body {
  font-size: calc(0.05vw + 16px);
}

.hidden {
  display: none;
}

#form-overlay {
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  justify-content: center; 
  align-items: center;
}

#form-overlay {
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  justify-content: center; 
  align-items: center;

  background: rgba(0, 0, 0, 0.35);
}

#todo-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;

  padding: 2rem 2.5rem;
  background: white;

  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);

  min-width: 280px;
  max-width: 420px;
}

#todo-form>input {
  font-size: 1rem;
  padding: 0.3rem 0.5rem;
}

#todo-form>:last-child {
  display: flex;
  justify-content: space-around;
}

#todo-form>:last-child>button {
  width: 5vw;
  aspect-ratio: 4 / 1;
  padding: 0.3rem;
  border-radius: 1rem;
}

.project{
  padding: 1vw;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.todo {
  display: flex;
  border: solid 2px black;

}

.container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5vh 1vw;
  flex: 20;
}

.container>.complete-state {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  height: 2rem;
  aspect-ratio: 1/1;
  box-sizing: border-box;
  background-color: transparent;
  border-style: solid;
  border-radius: 1em;
  border-width: 1px;
  cursor: default;
  user-select: none;
}

.todo-button {
  font-size: 1em;
  padding: 0.5rem;
  border-radius: 1rem;
}

.delete-button {
  flex: 1;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
  user-select: none;
  border-left: solid 1px black;
}

.project-text {
  font-size: 2rem;
  font-weight: bold;
}

nav>:last-child {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.project-button {
  font-size: 1.2rem;
  background-color: white;
  padding: 0.2rem;
  border: 4px black solid;
  cursor: default;
  user-select: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;AACA,SAAS;AACT,UAAU;AACV;;AAEA;EACE,gCAAgC;EAChC,YAAY;EACZ,iBAAiB;EACjB,oBAAoB;AACtB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,qBAAqB;EACrB,kBAAkB;EAClB,MAAM;EACN,SAAS;EACT,OAAO;EACP,kBAAkB;EAClB,sBAAsB;EACtB,WAAW;EACX,QAAQ;AACV;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,WAAW;AACb;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,eAAe;EACf,MAAM;EACN,OAAO;;EAEP,YAAY;EACZ,aAAa;;EAEb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,MAAM;EACN,OAAO;;EAEP,YAAY;EACZ,aAAa;;EAEb,uBAAuB;EACvB,mBAAmB;;EAEnB,+BAA+B;AACjC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;;EAET,oBAAoB;EACpB,iBAAiB;;EAEjB,mBAAmB;EACnB,0CAA0C;;EAE1C,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,6BAA6B;AAC/B;;AAEA;EACE,UAAU;EACV,mBAAmB;EACnB,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,uBAAuB;;AAEzB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,kBAAkB;EAClB,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,eAAe;EACf,YAAY;EACZ,iBAAiB;EACjB,sBAAsB;EACtB,6BAA6B;EAC7B,mBAAmB;EACnB,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,cAAc;EACd,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,eAAe;EACf,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,eAAe;EACf,iBAAiB;EACjB,4BAA4B;AAC9B;;AAEA;EACE,eAAe;EACf,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,uBAAuB;EACvB,eAAe;EACf,uBAAuB;EACvB,eAAe;EACf,iBAAiB;AACnB","sourcesContent":["* {\nmargin: 0;\npadding: 0;\n}\n\nbutton {\n  background-color: cornflowerblue;\n  color: white;\n  font-weight: bold;\n  border-style: hidden;\n}\n\nnav {\n  display: flex;\n  flex-direction: column;\n  background-color: red;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  padding: 0.5vw 1vw;\n  box-sizing: border-box;\n  width: 10vw;\n  gap: 3vh;\n}\n\nmain {\n  position: relative;\n  left: 10vw;\n  width: 90vw;\n}\n\nbody {\n  font-size: calc(0.05vw + 16px);\n}\n\n.hidden {\n  displa